<template>
  <div class="snacks">
    <scroll class="menu-wrapper"
            :data="foodsList"
            ref="menuWrapper">
      <ul>
        <li 
          v-for="(item,index) in foodsList" 
          :key="index" class="menu-item" 
          :class="{'current': currentIndex === index}" 
          @click="selectMenu(index,$event)"
          >
          <span class="text">
							{{item.name}}
					</span>
        </li>
      </ul>
      <div class="loading-wrapper"></div>
    </scroll>
    <scroll class="foods-wrapper"
            :data="foodsList"
            ref="foodsWrapper"
            :probeType="3"
            :listenScroll="true"
            @scroll="foodsScroll">
      <ul>
        <li v-for="(item,index) in foodsList" :key="index" class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li v-for="(food,index) in item.foods" class="food-item" :key="index">
              <div class="icon">
									<img :src="food.image" width="57" height="57" />
							</div>
              <div class="content">
                
									<h2 class="name">{{food.name}}</h2>
                  <p class="desc">{{food.description}}</p>
                  
                  <div class="extra">
										<span class="count">月售{{food.sellCount}}份</span>
										<span>好评率{{food.rating}}</span>
									</div>
                  <div class="cartControl-wrapper">
										<cartcontrol @add="addFood" :food="food"></cartcontrol>
									</div>
									<div class="price">
										<span class="now">¥{{food.price}}</span><span class="old" v-show="food.oldPrice">¥{{food.oldPrice}}</span>
									</div>
                  
								</div>
            </li>
          </ul>
        </li>
      </ul>
      <div class="loading-wrapper"></div>
    </scroll>
    <shopcart ref="shopcart" :select-foods="selectFoods" ></shopcart>
  </div>
</template>
<script>
import { scroll, cartcontrol, shopcart } from "@/components";
export default {
  name: "goods",
  data() {
    return {
      foodsList: [],
      listHeight: [],
      scrollY: 0,
      _initHeight: null
    };
  },
  components: {
    scroll,
    cartcontrol,
    shopcart
  },
  computed: {
    currentIndex() {
      //根据滚动高度处于foods-list高度某个区间内，得到当前索引
      for (let i = 0, l = this.listHeight.length; i < l; i++) {
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i + 1];
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          return i;
        }
      }
      return 0;
    },
    selectFoods() {
      let foods = [];
      this.foodsList.forEach(item => {
        item.foods.forEach(food => {
          if (food.count) {
            foods.push(food);
          }
        });
      });
      return foods;
    }
  },
  created() {
    this.loadData();
  },
  methods: {
    drop(target) {
      //体验优化，异步执行下落动画
      this.$nextTick(() => {
        this.$refs.shopcart.drop(target);
      });
    },
    addFood(target) {
      this.drop(target);
    },
    foodsScroll(pos) {
      //获取food-list滚动高度
      this.scrollY = Math.abs(Math.round(pos.y));
      //food-list滚动，菜单指向对应项
      let menuList = this.$refs.menuWrapper.$el.getElementsByClassName(
        "menu-item"
      );
      this.$refs.menuWrapper.scrollToElement(
        menuList[this.currentIndex],
        300,
        0,
        this._initHeight
      );
    },
    _countHeight() {
      //初始化设置高度-使menu当前索引切换时，food类别抵达屏幕可视区域中部
      this._initHeight = -Math.round(
        this.$refs.foodsWrapper.$el.clientHeight / 3
      );
      //计算food-list的高度
      let foodList = this.$refs.foodsWrapper.$el.getElementsByClassName(
        "food-list-hook"
      );
      let height = this._initHeight;
      this.listHeight.push(height);
      for (let i = 0, l = foodList.length; i < l; i++) {
        let item = foodList[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    },
    selectMenu(index, e) {
      //e._constructed，阻止非vue事件
      if (!e._constructed) {
        return;
      }
      let foodList = this.$refs.foodsWrapper.$el.getElementsByClassName(
        "food-list-hook"
      );
      let el = foodList[index];
      this.$refs.foodsWrapper.scrollToElement(el, 300, 0, this._initHeight);
    },
    loadData() {
      this.axios
        .get("/api/goods")
        .then(res => {
          this.foodsList = res.data.data.concat(this.foodsList);
          this.$nextTick(() => {
            this._countHeight();
          });
        }) //失败返回
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.snacks {
  display: flex;
  position: absolute;
  top: 175px;
  bottom: 46px;
  width: 100%;
  overflow: hidden;

  .menu-wrapper {
    flex: 0 0 80px;
    width: 80px;
    background: #f3f5f7;
    .menu-item {
      display: table;
      height: 54px;
      width: 56px;
      padding: 0 12px;
      line-height: 14px;
      .text {
        display: table-cell;
        width: 56px;
        vertical-align: middle;
        font-size: 12px;
      }
      &.current {
        position: relative;
        z-index: 10;
        margin-top: -1px;
        background: #fff;
        border-left: 3px solid #2d8cf0;
        .text {
          color: #00a0dc border-none;
        }
      }
    }
  }

  .foods-wrapper {
    flex: 1;
    background: #fff;
    .title {
      padding-left: 14px;
      height: 26px;
      line-height: 26px;
      border-left: 2px solid #d9dde1;
      font-size: 12px;
      color: rgb(147, 153, 159);
      background: #f3f5f7;
    }
    .food-item {
      display: flex;
      margin: 18px;
      // padding-bottom: 9px;
      border-bottom: 1px solid #e9eaec;
      &:last-child {
        border: none;
        margin-bottom: 0;
      }
      .icon {
        flex: 0 0 57px;
        margin-right: 10px;
      }
      .content {
        flex: 1;
        .name {
          margin: 2px 0 8px 0;
          height: 14p;
          line-height: 14px;
          font-size: 14px;
          color: rgb(7, 17, 27);
        }
        .desc,
        .extra {
          font-size: 10px;
          color: rgb(147, 153, 159);
        }
        .desc {
          margin-bottom: 8px;
          line-height: 12px;
        }
        .extra {
          line-height: 10px;
          .count {
            margin-right: 12px;
          }
        }
        .price {
          font-weight: 700;
          line-height: 36px;
          .now {
            margin-right: 8px;
            font-size: 14px;
            color: rgb(240, 20, 20);
          }
          .old {
            text-decoration: line-through;
            font-size: 10px;
            color: rgb(147, 153, 159);
          }
        }
        .cartControl-wrapper {
          position: absolute;
          right: 15px;
          // top: 92px;
        }
      }
    }
  }
}
</style>



